import * as React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Grid } from '@material-ui/core';
import AuthService from '../AuthService';
import apiservice from '../apiservice';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));


const ro = [
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"},
    { domainName: 'Snow', bns: "Buy", Status:"Active", OfferedAt: "200"}
    
];

export default function Negotiations() {

  let [rows,setRows]= React.useState([]);

  React.useEffect(()=>{
    let id = AuthService.getCurrentId();
    apiservice.getNegotiations(id).then((response)=>{setRows(response.data);}).catch((error)=>console.log(error.response));
  
  })

  return (
    <Grid
  container
  spacing={0}
  direction="column"
  alignItems="center"
  justifyContent="start"
  style={{ minHeight: '100vh',marginTop:40}}
>
    <h1>Your Negotiations</h1>
    <TableContainer component={Paper} sx={{ maxWidth: 800 }}>
      <Table sx={{ maxWidth: 800 }} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Domain-Name</StyledTableCell>
            <StyledTableCell align="right">Buy/Sell</StyledTableCell>
            <StyledTableCell align="right">Status</StyledTableCell>
            <StyledTableCell align="right">Offered-At</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <StyledTableRow key={row.domainName}>
              <StyledTableCell component="th" scope="row">
                {row.domainName}
              </StyledTableCell>
              <StyledTableCell align="right">{row.bns}</StyledTableCell>
              <StyledTableCell align="right">{row.status}</StyledTableCell>
              <StyledTableCell align="right">{row.offeredAt}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </Grid>
  );
}
