import { Button, Container, Grid, Stack,Typography } from "@mui/material";
import { Field, Form, Formik } from "formik";
import * as Yup from "yup"
import React, { Component, useState } from "react";
import { TextField } from "formik-material-ui"
import { useNavigate, useParams } from "react-router-dom";
import apiservice from "../apiservice";
import AuthService from "../AuthService";


    const validationSchema = Yup.object().shape({price: Yup.number().required("Required").min(200, "Must be more than 200")});
    
function MakeOffer()
{

const navigate = useNavigate();
    
    
    const [price, setPrice] = useState(200);

    let { sdomain } = useParams();

    

    const onSubmit=(values)=>
    {
        setPrice(values.price);
        console.log(price);
        if(AuthService.isUserLoggedIn())
        {
            let id= AuthService.getCurrentId();
            apiservice.uploadNegotiation(id,sdomain,values.price).then((response)=>{console.log(`Domain :${sdomain} uploaded with price : ${values.price}`); alert(`Made offer of a domain :${sdomain} with price : ${values.price}`)}).catch((error)=>console.log(error.response))}
        else
        {
            console.log("please login")
            navigate("/login")
        }
    }

        
        
        return (

            <Container sx={{margin:10}}>
            <Stack direction="column" justifyContent="center" spacing={3} sx={{}}>
             <Typography component="h1" color="skyblue" variant="h3" sx={{fontWeight: 'bold'}}>
              {sdomain}
             </Typography>
             <Typography component="h2" variant="h4">
May be for sale.
             </Typography>
             <Typography component="h2" color="text.secondary" variant="subtitle">
Open a negotiation for 20USD and your offer will be presented guaranteed.
             </Typography>
             <Stack direction="row" >
{/*<TextField></TextField>*/}
             <Formik initialValues={{price}}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
            enableReinitialize={true}>{
({ dirty, isValid, values, handleChange, handleBlur }) => {
              return (
                <Form>
            <Grid container direction="row" spacing={3
            } justifyContent="center" sx={{marginTop:-1}}>
                    <Grid item><Field
                          label="Offer Price"
                          variant="outlined"
                          fullWidth
                          name="price"
                         
                          component={TextField}
                        /></Grid>
                        <Grid item>
                        <Button sx={{padding:2}}
                      disabled={!dirty||!isValid}
                      variant="contained"
                      color="primary"
                      type="Submit"
                     >
                      Place Your Offer
                    </Button>
                        </Grid>
                    </Grid>
                </Form>
              )}}
             </Formik>
             </Stack>
            </Stack>
            </Container >
            
        );
    }


export default MakeOffer;