import * as React from "react";
  
// importing material UI components
import { AppBar } from "@material-ui/core";

import {Toolbar} from "@material-ui/core";
//import Typography from "@mui/material/Typography";
import { Box, Button, Container, Slide } from "@mui/material";
import {IconButton} from "@material-ui/core";
//import { Menu } from "@material-ui/icons";
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { useState } from "react";
import AuthService from "../AuthService";
import { useNavigate } from "react-router-dom";
  
export default function Header() {
  const [anchor, setAnchor] = useState(null);

  const openMenu = (event) => {
    setAnchor(event.currentTarget);
  };

  const handleClose = () => {
    setAnchor(null);
  };

  const updomain = () => {
    setAnchor(null);
    navigate("/uploadDomains");
  };

  const mdomains = () => {
    setAnchor(null);
    navigate("/domainsList");

  };

  const isUserLoggedIn = AuthService.isUserLoggedIn();
  const navigate = useNavigate();

  return (

    
    
      <AppBar position="static" color="transparent">
        <Toolbar>
          {/*Inside the IconButton, we 
           can render various icons*/}
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            {/*This is a simple Menu 
             Icon wrapped in Icon */}
            <Menu />
          </IconButton>
          {/* The Typography component applies 
           default font weights and sizes */}
  
          {/*<Typography variant="h6" 
            component="div" sx={{ flexGrow: 1 }}>
            NameKart
          </Typography>*/}
          <Button style={{fontSize: 20}} color="inherit" onClick={()=>navigate("/home")}>NameKart</Button>
          <Box display='flex' flexGrow={1}>
            {/* whatever is on the left side */}
        </Box >
        {isUserLoggedIn&&<Box display="flex" flexDirection="row">
        <Box sx={{marginRight:2}}>
<Button color="inherit" onClick={()=>navigate("/negotiations")}>Negotiations</Button>
        </Box>

        <Box sx={{ marginRight: 2 }}>
      <Button
        id="basic-button"
        color="inherit"
        onClick={openMenu}
        
      >
        Domains
      </Button>
      <Menu
        id="basic-menu"
        open={Boolean(anchor)}
        anchorEl={anchor}
        onClose={handleClose}
        TransitionComponent={Slide}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        <Box display="flex" flexDirection="column" m={1} >
        <MenuItem onClick={updomain}><Button color="inherit">Upload Domains</Button></MenuItem>
        <MenuItem onClick={mdomains}><Button>My Domains</Button></MenuItem>
        </Box>
      </Menu>
      </Box>
      </Box>}
      {!isUserLoggedIn&&<div>
          <Button color="inherit" onClick={()=>navigate("/signup")}>Sign-Up</Button>
          <Button color="inherit" onClick={()=>navigate("/login")}>Login</Button>
          </div>}
          {isUserLoggedIn&&<Box sx={{marginRight:2}}>
<Button color="inherit" onClick={()=>{AuthService.logout(); navigate("/home")}}>Log Out</Button>
        </Box>}
        </Toolbar>
      </AppBar>
  );
}